//======================================================================
// Shades of gray
// @links https://en.wikipedia.org/wiki/Shades_of_gray
//======================================================================

$shades-of-gray: (
  // achromatic grays - rgb values are exactly equal.
  'gray': #808080, // half of rgb values.
  'gainsboro': #dcdcdc,
  'lightgray': #d3d3d3,
  'silver': #c0c0c0,
  'mediumgray': #bebebe, // half of between light gray and dark gray.
  'darkgray': #a9a9a9,
  'spanishgray': #989898,
  'dimgray': #696969,
  'davysgray': #555,
  'jet': #343434,

  // off-grays - rgb values are not exactly equal.
  'platinum': #e5e4e2,
  'ashgray': #b2beb5,
  'battleshipgray': #848482,
  'gunmetal': #2a3439,
  'nickel': #727472,
  'charcoal': #36454f,

  // cool grays
  'coolgray': #8c92ac,
  'cadetgray': #91a3b0,
  'bluegray': #69c,
  'glaucous': #6082b6,
  'slategray': #708090,
  'graygreen': #5e716a,
  'marengo': #4c5866,

  // warm grays
  'puce': #c89,
  'rosequartz': #aa98a9,
  'cinereous': #98817b,
  'rocketmetallic': #8a7f8d,
  'taupe': #483c32,

  // web colors
  // https://www.w3schools.com/colors/colors_groups.asp
  'lightslategray': #789,
  'darkslategray': #2f4f4f,
);



