$shades-of-cyan: (
  'cyan': #0ff,
  'aqua': #0ff,
  'pigmentcyan': #00b7eb, // CMYK

  // Variations.
  'aeroblue': #c9ffe5,
  'aquamarine': #7fffd4,
  'bluegreen': #0d98ba,
  'caribbeancurrent': #006d6f,
  'celeste': #b2ffff,
  'cerulean': #007ba7,
  'charlestongreen': #232b2b,
  'darkcyan': #008b8b,
  'electricblue': #7df9ff,
  'greenblue': #1164b4,
  'keppel': #3ab09e,
  'lightcyan': #e0ffff,
  'lightseagreen': #20b2aa,
  'midnightgreen': #004953,
  'robineggblue': #0cc,
  'crayolaskyblue': #80daeb, // Crayola
  'teal': #008080,
  'tiffanyblue': #81d8d0, // Tiffany & Company
  'turquoise': #40e0d0,
  'verdigris': #43b3ae,
  'viridian': #40826d,

  // Web Colors.
  // https://www.w3schools.com/colors/colors_groups.asp
  'paleturquoise': #afeeee,
  'mediumturquoise': #48d1cc,
  'darkturquoise': #00ced1,
);



