//======================================================================
// Shades of yellow
// @links https://en.wikipedia.org/wiki/Shades_of_yellow
//======================================================================

$shades-of-yellow: (
  'yellow': #ff0,

  // Additional definitions
  'pigmentyellow': #ffef00, // CMYK
  'naturalyellow': #ffd300, // NCS
  'munsellyellow': #efcc00, // Munsell
  'pantoneyellow': #fedf00, // Pantone
  'crayolayellow': #fce883, // Crayola

  // Variations
  'canaryyellow': #ff9,
  'cloverlime': #fce883,
  'unmellowyellow': #ff6,
  'lemon': #fdff00,
  'maximumyellow': #fafa37,
  'mellowyellow': #f8de7e,
  'royalyellow': #fada5e,
  'gold': #ffd700,
  'cyberyellow': #ffd300,
  'safetyyellow': #eed202,
  'khaki': #c3b091,
  'tromboneyellow': #d2b55b, // Deprecated at 2017-10-29
  'goldenrod': #daa520,
  'hunyadiyellow': #e8ac41,

  // tints of yellow
  'lightyellow': #ffffe0,
  'cream': #ffc,
  'lemonchiffon': #fffacd,

  // Web colors
  // https://www.w3schools.com/colors/colors_groups.asp
  'lightgoldenrodyellow': #fafad2,
  'papayawhip': #ffefd5,
  'moccasin': #ffe4b5,
  'peachpuff': #ffdab9,
  'palegoldenrod': #eee8aa,
  'x11khaki': #f0e68c,
  'darkkhaki': #bdb76b,
);



